.month {
    padding: 10px 15px;
    width: 100%;
    background: #1abc9c;
    text-align: center;
}
  
.month ul {
    margin: 0;
    padding: 0;
}

.month ul li {
    color: white;
    font-size: 20px;
    text-transform: uppercase;
    letter-spacing: 3px;
}

.month .prev {
    float: left;
    cursor:pointer;
}

.month .next {
    float: right;
    cursor:pointer;
}

.weekdays {
    margin: 0;
    padding: 10px 0;
    background-color: #ddd;
}

.weekdays li {
    display: inline-block;
    width: 13.6%;
    color: #666;
    text-align: center;
}

.days {
    padding: 10px 0;
    background: #eee;
    margin: 0;
}

.days li {
    list-style-type: none;
    display: inline-block;
    width: 13.6%;
    text-align: center;
    margin-bottom: 0px;
    font-size:12px;
    color: #777;
}

.days li .active {
    display: inline-block;
    background: #1abc9c;
    width: 100%;
    color: white !important
}

.days li:hover {
    display: inline-block;
    background: #1ee4bc;
    width: 13.6%;
    color: white !important
}